.app{
    height: 100vh;
    
    .section{
        width: 100%;
        height:calc(100vh - 60px);
        background-color: red;
        position: relative;
        top: 60px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //for firefox
        &::-webkit-scrollbar{
            display: none;
        }

        >*{
            width: 100vw;
            height: calc(100vh - 60px);
            scroll-snap-align: start;
        }
    }
}